import { useState } from 'react';
import InputSection from './components/InputSection/InputSection';
import ItemList from './components/ItemsSection/ItemsList';
import { Heading } from '@chakra-ui/react'
import { SunIcon, MoonIcon } from '@chakra-ui/icons';
import { Button } from '@chakra-ui/react';
import { useColorMode } from '@chakra-ui/react';
import { v4 as uuidv4 } from 'uuid';
import { motion } from "framer-motion"
import './App.css';

function App() {
  const { colorMode, toggleColorMode } = useColorMode();
  const [ tasks, setTasks ] = useState([]);

  const addTaskHandler = (task) => {
    setTasks((prevTasks) => [
      ...prevTasks,
      {task: task,
      id: uuidv4(),
      isDone: false}
    ]);
  };

  const removeTaskHandler = (id) => {
    setTasks(tasks.filter((task) => task.id !== id));
  };

  const onDoneHandler = (id) => {
    setTasks((prevTasks) => {
     return prevTasks.map((task) => task.id === id ? {...task, isDone: !task.isDone} : task)
    });
  };

  return (
    <motion.div 
      className="App" 
      initial={{ opacity: 0 }}
      animate={{ opacity: 1 }}
      exit={{ opacity: 0 }}
      transition={{ ease: "easeOut", duration: 1.5 }}>
      <Heading 
        size='xl' 
        fontSize='38px'>What I should to do</Heading>
      <InputSection onAddTask={addTaskHandler} />
      <ItemList tasks={tasks} onRemove={removeTaskHandler} onDone={onDoneHandler}/>
    <motion.div  whileTap={{ scale: 0.8 }}>
      <Button 
        variant='outline' 
        onClick={toggleColorMode}>{colorMode === 'light' ? <MoonIcon boxSize={4}/> : <SunIcon boxSize={4}/>}</Button>
    </motion.div>
    </motion.div>
   
  );
}

export default App;
